:root {
  --bg-dark: rgb(18, 20, 26);
  --bg-light: rgb(216, 216, 208);
}

.side-menu {
  position: absolute;
  z-index: 4;
  margin: max(30px, 3vh) 0 0 max(30px, 1.5vw);
  width: fit-content;
  height: auto;
  z-index: 99;
  filter: grayscale(1);
}

.menu-bar-line {
  display: none;
  width: max(50px, 2vw);
  /* adjusted in menuAnimation */
  height: max(6.5vh, 30px);
  opacity: 0.25;
  margin-top: max(0.5vh, 5px);
  margin-bottom: max(0.5vh, 5px);
}

.menu-item {
  display: flex;
  align-items: center;
}

.nav-hint {
  display: flex;
  align-items: center;
  color: rgb(216, 216, 216);
}

.nav-hint span {
  font-family: 'Reenie Beanie';
  font-size: max(2vh, 24px);
}

.nav-arrow {
  height: max(3.5vh, 40px);
  filter: invert(1);
  margin-left: 7.5px;
  margin-right: 5px;
}

.menu-toggle {
  width: max(50px, 2vw);
  cursor: pointer;
}

#opened-book {
  display: none;
}

.menu-icon {
  display: none;
  width: max(50px, 2vw);
  cursor: pointer;
}

.menu-tag {
  display: none;
  margin-left: max(15px, 0.5vw);
  font-size: max(1.5vh, 16px);
  color: rgb(255, 255, 255);
  padding: max(0.25vh, 2px) max(1vw, 20px) max(0.25vh, 2px) max(1vw, 20px);
  border-radius: 10vh;
  border: 2px solid white;
  background-color: rgba(18, 20, 26, 1);
}

.menu-toggle:hover + .menu-tag {
  display: block;
}

.menu-toggle:hover {
  filter: drop-shadow(0px 0px max(8px, 1.5vh) rgb(255, 255, 255));
}

.menu-icon:hover + .menu-tag {
  display: block;
}

.menu-icon:hover {
  filter: drop-shadow(0px 0px max(8px, 1.5vh) rgb(255, 255, 255));
}

.side-menu.light {
  filter: grayscale(1) invert(1);
}

.return-to-main-logo {
  position: fixed;
  bottom: 10px;
  left: 10px;
  height: 40px;
  width: 40px;
  background-image: url("../../assets/logo2.png");
  background-size: cover;
  cursor: pointer;
  background-color: white;
  border-radius: 30px;
}
